<template>
    <div class="v-main-wrapper">
        <h1>{{title}}</h1>
        <v-catalog />
        <v-cart
            v-if="CART.length"
            :cart_data="CART"
        />
    </div>
</template>

<script>
    import vCatalog from './v-catalog'
    import vCart from './v-cart'
    import {mapGetters} from 'vuex'

    export default {
        name: 'v-main-wrapper',
        components:{
            vCatalog,
            vCart
        },
        props: {},
        data(){
            return {
                title: 'Magazin'
            }
        },
        computed: {
            ...mapGetters([
                'CART'
           ])
        },
        methods: {},
        watch: {},
        mounted (){
            console.log('Hello!')
        }
    }
</script>

<style>
    .v-main-wrapper{
        max-width: 1000px;
        margin: 0 auto;
    }
</style>